<template>
  <BaseModal v-model="localModelValue">
    <div class="layout-simple">
      <MintModalHero @close="localModelValue = false" />
      <div class="layout-simple__content">
        <div class="layout-simple-page">
          <Transition
              name="fade"
              mode="out-in"
          >
            <MintModalAuth v-if="!address || screen === Screens.Auth && error === null" />
            <MintModalMain
                v-else-if="screen === Screens.Mint && error === null"
                :eligible-data="eligibleData"
                :supply="supply"
                @no-money="error = ErrorStatus.NotEnoughMoney"
                @success="screen = Screens.Success"
                @fail="error = ErrorStatus.SomethingWrong"
            />
            <MintModalStatusFail
              v-else-if="error !== null"
              :error="error"
              @try-again="failTryAgain"
              @retry="error = null"
            />
            <MintModalStatusSuccess v-else-if="screen === Screens.Success" />
          </Transition>
        </div>
      </div>
    </div>
  </BaseModal>
</template>

<script setup lang="ts">
import {computed, ref, watch} from 'vue';
import {useAccount, useNetwork} from 'use-wagmi';
import {getContract} from 'use-wagmi/actions';
import {BaseModal} from '@/shared/ui/base-modal';
import {useHttpClient} from '@/shared/api';
import {
  ErrorStatus,
  MintModalAuth,
  MintModalHero,
  MintModalMain,
  MintModalStatusFail,
  MintModalStatusSuccess,
} from './ui';
import VastegaHarpisABI from '@/app/abi/VastegaHarips';
import {MINT_TOTAL_SUPPLY} from './ui/mint-config';


interface Props {
  modelValue: boolean;
}

interface Emits {
  (e: 'update:model-value', value: Props['modelValue']): void,
}

const props = defineProps<Props>();

const emits = defineEmits<Emits>();

enum Screens {
  Auth = 'auth',
  Mint = 'mint',
  Success = 'success',
}

const error = ref<ErrorStatus | null>(null);

const screen = ref<Screens>(Screens.Auth);

const supply = ref(0);

const { address } = useAccount();

const localModelValue = computed({
  get() {
    return props.modelValue;
  },
  set(value) {
    emits('update:model-value', value);
  },
});

const eligibleData = ref({
  amount: 0,
});

const checkPresaleEligible = async (): Promise<boolean> => {
  const httpClient = useHttpClient();

  // try {
  //   const data = await httpClient('/presale_eligible', {
  //     params: {
  //       address: address.value,
  //     }
  //   });
  //
  //   eligibleData.value.amount = data.amount;
  // } catch (e) {
  //   console.log(e);
  // } finally {
  //   if (eligibleData.value.amount === 0) {
  //     error.value = ErrorStatus.NotInWhitelist;
  //   }
  // }

  eligibleData.value.amount = 5;

  return eligibleData.value.amount !== 0;
}

watch(address, checkAll);

async function checkAll() {
  if (!checkNetwork()) {
    return;
  } else if (!(await checkWhiteListAndSupply())) {
    return;
  } else if (!(await checkPresaleEligible())) {
    return;
  }

  screen.value = Screens.Mint;
  error.value = null;
}

async function checkWhiteListAndSupply() {
  const contract = await getContract({
    abi: VastegaHarpisABI,
    address: '0x1cB5E5C082AF921A128980BFbD0c3AF0076b5aE0',
  });

  const wlUsed = await contract.read.wlUsed([address.value]);
  supply.value = Number(await contract.read.supply());

  if (wlUsed) {
    screen.value = Screens.Success;
  }

  if (supply.value === MINT_TOTAL_SUPPLY) {
    error.value = ErrorStatus.TokensSoldout;
  }

  return !wlUsed && supply.value !== MINT_TOTAL_SUPPLY;
}

function failTryAgain() {
  error.value = null;
  checkAll();
}

const { chain, chains } = useNetwork();

watch(chain, () => {
  checkNetwork();
});

function checkNetwork() {
  const isCorrect = chains.value.find((item) => item.id === chain?.value.id);

  error.value = isCorrect ? null : ErrorStatus.WrongChain

  return isCorrect;
}
</script>

