import type { SearchParameters } from 'ofetch';
import type { HttpParamCortege } from './types';

const paramsFilter = ([, value]: HttpParamCortege): boolean => {
  if (typeof value === 'string' || Array.isArray(value)) {
    return value.length > 0;
  }

  if (typeof value === 'boolean') {
    return value;
  }

  return typeof value === 'number';
};

const toSafeValue = (value: string): string => value.toString().replace(/,/gi, '%2C');

const paramsArraySerializer = ([key, rawValue]: HttpParamCortege): HttpParamCortege => {
  let value;

  if (Array.isArray(rawValue)) {
    value = rawValue.map(toSafeValue).join(',');
  } else {
    value = rawValue;
  }

  return [key, value];
};

export const httpParamsSerializer = (params: SearchParameters): SearchParameters => {
  const entries = Object.entries(params)
    .filter(paramsFilter)
    .map(paramsArraySerializer);

  return Object.fromEntries(entries);
};
