<template>
  <div
    class="home-slider"
    @click="mint"
  >
    <div
      ref="slider"
      class="home-slider__images"
    >
      <img
        v-for="index in Number($t('slider.countImages'))"
        :key="index"
        :srcset="`/images/slider/slide${index}@2x.png 2x`"
        :src="`/images/slider/slide${index}.png`"
        :class="{ 'home-slider__image--active': index === indexActiveSlide }"
        :alt="`Character ${index}`"
        class="home-slider__image"
      >
    </div>
    <h4 class="home-slider__mint">
      Mint now
    </h4>
    <BaseButton
      class="home-slider__soon__btn"
      appearance="secondary"
      no-background
      @click="$emit('mint')"
    >
      {{ $t('slider.button') }}
    </BaseButton>
  </div>
</template>

<script setup lang="ts">
import {onMounted, ref} from 'vue'

import type {Ref} from 'vue'

import {BaseButton} from '@/shared/ui/base-button'

interface Emits {
  (e: 'mint'): void
}

const emits = defineEmits<Emits>();

const TICK_TIME = 150

const slider: Ref<null> = ref(null)
const indexActiveSlide: Ref<number> = ref(1)

const changeSlide = () => {
  if (indexActiveSlide.value === 5)
    indexActiveSlide.value = 0

  indexActiveSlide.value = indexActiveSlide.value + 1
}

onMounted(() => {
  setInterval(() => {
    changeSlide()
  }, TICK_TIME)
})

function mint() {
  if (window.innerWidth <= 650) {
    emits('mint');
  }
}
</script>

