<template>
  <div class="home-player">
    <div class="home-player__control">
      <button
        title="Button Play/Pause"
        class="home-player__button"
        @click="clickHandler"
      >
        <IconPause v-if="!isPaused"/>
        <IconPlay v-else/>
      </button>
      <div class="home-player__ticker-wrap">
        <div id="ticker" class="home-player__ticker" :class="isIos ? 'ios-ticker' : ''">
          <div class="home-player__ticker-item">
            {{ nameActiveTrack }}
          </div>
        </div>
      </div>
    </div>
    <canvas
      v-show="!isPaused"
      id="canvas"
      width="136"
      height="19"
      :class="{ 'home-player__equalizer--hidden': isPaused }"
      class="home-player__equalizer"
    />
    <div
      v-if="isPaused"
      class="home-player__equalizer--paused"
    >
      <IconPlayer/>
    </div>
  </div>
</template>

<script setup lang="ts">
import {computed, onMounted, ref} from 'vue'

import type {Ref} from 'vue'

import {Rive} from '@rive-app/canvas'
import IconPlayer from '@/assets/icons/IconPlayer.vue'
import IconPlay from '@/assets/icons/IconPlay.vue'
import IconPause from '@/assets/icons/IconPause.vue'

const trackList = [
  'INTRO OUTRO NORCHE',
  'Unbreak  NORCHE',
  'Secret Garden  NORCHE',
  'It can happen  NORCHE',
  'Crossroads  NORCHE',
]

const player: Ref<HTMLAudioElement | null> = ref(null)
const isPaused: Ref<boolean> = ref(true)
const indexActiveTrack: Ref<number> = ref(1)
const nameActiveTrack: Ref<string> = ref(trackList[0])
const isIos = computed(() => /iPad|iPhone|iPod/.test(navigator.userAgent))

const createAudio = () => {
  player.value = new Audio('/tracks/1.mp3')

  player.value.addEventListener('ended', () => {
    if (indexActiveTrack.value === trackList.length) {
      indexActiveTrack.value = 1
      nameActiveTrack.value = trackList[0]
    } else {
      indexActiveTrack.value = indexActiveTrack.value + 1
      nameActiveTrack.value = trackList[indexActiveTrack.value - 1]
    }

    if (!player.value)
      return

    player.value.src = `/tracks/${indexActiveTrack.value}.mp3`

    player.value.pause()
    player.value.load()
    player.value.play()
  })
}

const clickHandler = () => {
  if (!player.value)
    createAudio()

  isPaused.value = !isPaused.value

  if (player.value) {
    if (isPaused.value)
      player.value.pause()
    else player.value.play()
  }
}

onMounted(() => {
  new Rive({
    src: '/animations/equalizer.riv',
    canvas: document.getElementById('canvas'),
    autoplay: true,
  })
})
</script>

