<script setup>
import { computed, ref } from 'vue'

import { BaseTooltip } from '@/shared/ui/base-tooltip'

const orotai = ref(null)
const orotaiActive = ref(false)

const otai = ref(null)
const otaiActive = ref(false)

const haralugs = ref(null)
const haralugsActive = ref(false)

const harips = ref(null)
const haripsAnimated = ref(false)
const haripsBlurred = computed(() => orotaiActive.value || otaiActive.value || haralugsActive.value)

const checkHovered = (event, element, displaceLeft, cropWidth) => {
  const mouseX = event.clientX
  const mouseY = event.clientY

  const elementParams = element.getBoundingClientRect()
  const elementTop = elementParams.top
  const elementWidth = cropWidth ? elementParams.width - (elementParams.width * 0.5) : elementParams.width
  const elementHeight = elementParams.height + 400

  let elementLeft

  if (displaceLeft)
    elementLeft = elementParams.left + (elementParams.left * 0.1)
  else if (cropWidth)
    elementLeft = elementParams.left + (elementParams.left * 0.6)
  else elementLeft = elementParams.left

  return (
    mouseX >= elementLeft
    && mouseY >= elementTop
    && mouseX <= elementLeft + elementWidth
    && mouseY <= elementTop + elementHeight
  )
}
const isFlipped1 = ref(false)
const isFlipped2 = ref(false)
const isFlipped3 = ref(false)
const isFlipped4 = ref(false)

const resetCard1 = () => {
  isFlipped1.value = false
}
const resetCard2 = () => {
  isFlipped2.value = false
}
const resetCard3 = () => {
  isFlipped3.value = false
}
const resetCard4 = () => {
  isFlipped4.value = false
}
const mouseMoveHandler = (e) => {
  haripsAnimated.value = checkHovered(e, harips.value, false, true)
  orotaiActive.value = checkHovered(e, orotai.value, true)
  otaiActive.value = checkHovered(e, otai.value, true)
  haralugsActive.value = checkHovered(e, haralugs.value)

  if (otaiActive.value)
    orotaiActive.value = false

  if (orotaiActive.value) {
    otaiActive.value = false
    haripsAnimated.value = false
  }

  if (haralugsActive.value)
    haripsAnimated.value = false
}

const mouseLeaveHandler = () => {
  haripsAnimated.value = false
  orotaiActive.value = false
  otaiActive.value = false
  haralugsActive.value = false
}
</script>

