<template>
  <Component
      :is="buttonComponent"
      class="base-button"
      :class="{
        'base-button--no-background': noBackground,
        'base-button--disabled': isDisabled,
        [`base-button--appearance-${appearance}`]: appearance,
        [`base-button--theme-${theme}`]: theme,
      }"
      v-bind="buttonAttributes"
  >
    <div
        v-if="preIcon && icon"
        v-html="icon"
    />
    <slot />
    <div
        v-if="!preIcon && icon"
        v-html="icon"
    />
  </Component>
</template>


<script setup lang="ts">
import { computed, ref, watchEffect } from 'vue';
import { RouterLink } from 'vue-router';
import type { RouteLocationRaw } from 'vue-router';
import type { ButtonTheme, ButtonAppearance} from './types';
import { BaseButtonType } from './constants';

interface ButtonProps {
  type?: 'submit' | 'button' | 'reset' | 'span';
}

interface LinkProps {
  to?: RouteLocationRaw;
  href?: string;
  external?: boolean;
  replace?: boolean;
  custom?: boolean;
  target?: '_blank' | '_parent' | '_self' | '_top';
  activeClass?: string;
  exactActiveClass?: string;
  ariaCurrentValue?: string;
}

interface Props extends LinkProps, ButtonProps {
  isDisabled?: boolean
  appearance?: ButtonAppearance
  theme?: ButtonTheme
  title?: string
  preIcon?: boolean
  iconName?: string;
  noBackground?: boolean;
}

const props = withDefaults(defineProps<Props>(), {
  isLink: false,
  isDisabled: false,
  appearance: 'primary',
  preIcon: false,
  noBackground: false,
  theme: 'primary',
  target: '_self',
  title: 'Open link',
});

const buttonType = computed(() => {
  if (typeof props.href === 'string' && props.href.search(/^(\/\/|http)/) === 0) {
    return BaseButtonType.SimpleLink;
  }

  if (props.href || props.to) {
    return BaseButtonType.Link;
  }

  if (props.type === 'span') {
    return BaseButtonType.Span;
  }

  return BaseButtonType.Button;
});

const buttonComponent = computed(() => {
  switch (buttonType.value) {
    case BaseButtonType.Link: return RouterLink;
    case BaseButtonType.SimpleLink: return 'a';
    case BaseButtonType.Button: return 'button';
    case BaseButtonType.Span: return 'span';
    default: return 'span';
  }
});

const buttonAttributes = computed(() => {
  switch (buttonType.value) {
    case BaseButtonType.Link: return {
      to: props.to,
      href: props.href,
      external: props.external,
      replace: props.replace,
      custom: props.custom,
      target: props.target,
      activeClass: props.activeClass,
      exactActiveClass: props.exactActiveClass,
      ariaCurrentValue: props.ariaCurrentValue,
    };
    case BaseButtonType.SimpleLink: return {
      href: props.href,
      external: props.external,
      target: props.target,
    };
    case BaseButtonType.Button: return {
      type: props.type || 'button',
      disabled: props.isDisabled,
    };
    default: return {};
  }
});

const icon = ref<string | Record<string, string>>('');

const iconsList = import.meta.glob('@/assets/icons/**/*.svg', {
  as: 'raw',
  eager: false,
});

const pullSvgIcon = async (): Promise<void> => {
  if (!props.iconName) {
    return;
  }

  try {
    const iconsListElement = iconsList[`/src/assets/icons/${props.iconName}.svg`];

    if (iconsListElement) {
      const rawIcon = await iconsListElement();

      icon.value = rawIcon;
    } else {
      // eslint-disable-next-line no-console
      console.error(
          `Icon '${props.iconName}' doesn't exist in './icons'`,
      );
    }
  } catch {
    // eslint-disable-next-line no-console
    console.error(
        `Icon '${props.iconName}' doesn't exist in './icons'`,
    );
  }
};

pullSvgIcon();

watchEffect(pullSvgIcon);
</script>


<style lang="scss">
