  @include reset-button;
  position: relative;
  font-size: 12px;
  line-height: 14px;
  letter-spacing: 0.56em;
  text-transform: uppercase;
  transition: all .4s ease;
  cursor: pointer;

  display: inline-flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;

  text-decoration: none;

  @media screen and (max-width: 499px) {
    @supports (-webkit-touch-callout: none) {
      font-size: 0.835vw;
    }
  }

  &__link {
    display: flex;
    align-items: center;
    gap: 16px;
    color: inherit;
    text-decoration: none;
    padding: 12px 20px;
  }
}

.base-button--appearance-primary {
  border-radius: 90px;
  gap: 16px;
  padding: 12px 20px;
  @media (max-width: 650px) {
    width: 55px;
    height: 40px;
  }
}

.base-button--appearance-primary.base-button--theme-primary {
  color: var(--black);
  background: var(--accent);
  &:hover {
    // background: var(--white);
  }
}

.base-button--appearance-primary.base-button--theme-secondary {
  color: var(--white);
  background: rgba(255, 255, 255, 0.14);
  -webkit-backdrop-filter: blur(20px);
  backdrop-filter: blur(20px);

  &:hover {
    color: var(--black);
    background: var(--white);
  }
}


.base-button--appearance-secondary {
  font-weight: 500;
  padding: 12px 20px;
}

.base-button--appearance-secondary.base-button--theme-primary {
  border-radius: 10px;
  box-shadow: 0 0 0 1px #FFA753;
  background: rgba(255, 167, 83, 0.05);
  backdrop-filter: blur(7px);
  color: #FFA753;
  &:hover {
    background: rgba(255, 167, 83, 0.15);
    box-shadow: 0 0 0 2px #FFA753;
  }
  &:active {
    background: rgba(255, 167, 83, 0.25);
    box-shadow: 0 0 0 2px #FFA753;
  }
}

.base-button--appearance-secondary.base-button--no-background {
  background: none;
  backdrop-filter: none;
}
</style>
