<template>
  <div>
    <img
      v-show="!isReady"
      width="505px"
      :src="`/animations/capsule/${FIRST_IMAGE}.png`"
      alt="Capsule"
    >
    <canvas
      v-show="isReady"
      id="capsule-canvas"
    />
  </div>
</template>

<script setup lang="ts">
import {onMounted, onUnmounted, ref} from 'vue';

const FIRST_IMAGE = 10001;

const images = [...Array(180)].map((item, index) => FIRST_IMAGE + index);

let timeout: typeof setTimeout = null;

const isReady = ref(false);

onMounted(async () => {
  const canvas = <HTMLCanvasElement>document.querySelector('#capsule-canvas');
  const ctx = canvas.getContext('2d');

  canvas.width = 505;
  canvas.height = 505;

  ctx.scale(0.5, 0.5)

  const imageItems = await Promise.all(images.map(loadImage));
  let currentIndex = 0;

  isReady.value = true;

  const change = () => {
    if (currentIndex < images.length - 1) {
      currentIndex += 1;
    } else {
      currentIndex = 0;
    }
  }

  function animate() {
    const fps = 45;

    ctx.clearRect(0, 0, 10000, 10000)
    ctx.drawImage(imageItems[currentIndex], 0, 0);
    change();

    timeout = setTimeout(() => {
      requestAnimationFrame(animate);
    }, 1000 / fps);
  }

  animate();
});

onUnmounted(() => {
  if (timeout) {
    clearTimeout(timeout);
  }
});

function loadImage(image) {
  return new Promise((resolve, reject) => {
      const img = new Image();

      img.src = `/animations/capsule/${image}.png`;

      img.onload = function (lol) {
        resolve(img);
      };
    }
  );
}
</script>

<style lang="scss">
#capsule-canvas {
  width: 100%;
}
</style>
