<template>
  <div class="base-tooltip">
    <h3 class="base-tooltip__title">
      {{ title }}
    </h3>
    <span class="base-tooltip__subtitle">
      {{ subtitle }}
    </span>

    <BaseButton
        v-if="showButton"
        class="base-tooltip__button"
        appearance="secondary"
        @click="$emit('mint')"
    >
      Mint now
    </BaseButton>

    <div
        class="base-tooltip__icon"
        @mouseover="mouseEnterHandler"
        @mouseout="mouseLeaveHandler"
    >
      <IconDot v-if="showIcon" />
    </div>

    <div
        :class="{ 'base-tooltip__panel--opened': isShowPanel }"
        class="base-tooltip__panel"
        @mouseover="mouseEnterHandler"
        @mouseout="mouseLeaveHandler"
    >
      <h3 class="base-tooltip__panel-title">
        {{ title }}
      </h3>
      <p class="base-tooltip__panel-text">
        {{ text }}
      </p>
    </div>
  </div>
</template>

<script setup lang="ts">
import { ref } from 'vue'

import type { Ref } from 'vue'

import { BaseButton } from '@/shared/ui/base-button'
import IconDot from '@/assets/icons/IconDot.vue'

interface Props {
  title?: string
  subtitle?: string
  text?: string
  showButton?: boolean
  isDisabled?: boolean
  showIcon?: boolean
  isLink?: boolean
}

withDefaults(defineProps<Props>(), {
  title: '',
  subtitle: '',
  text: '',
  showButton: false,
  isDisabled: false,
  showIcon: true,
  isLink: true,
})

const emits = defineEmits(['onMouseEnter', 'onMouseLeave'])

const isShowPanel: Ref<boolean> = ref(false)

const mouseEnterHandler = () => {
  isShowPanel.value = true
  emits('onMouseEnter')
}

const mouseLeaveHandler = () => {
  isShowPanel.value = false
  emits('onMouseLeave')
}
</script>

