<template>
  <MintStatus
    class="mint-modal-status-fail"
    :title="info.title"
    :text="info.text"
    status="fail"
  >
    <template
      v-if="error === ErrorStatus.NotInWhitelist"
      #default
    >
      <BaseButton
        class="mint-modal-status-fail__button"
        appearance="secondary"
        icon-name="link"
      >
        Discord
      </BaseButton>
      <BaseButton
        class="mint-modal-status-fail__button"
        appearance="secondary"
        icon-name="link"
      >
        Twitter
      </BaseButton>
    </template>
    <template
      v-else-if="error === ErrorStatus.TokensSoldout"
      #default
    >
      <BaseButton
        class="mint-modal-status-fail__button"
        appearance="secondary"
        icon-name="link"
      >
        Opensea
      </BaseButton>
    </template>
    <template
      v-else-if="error === ErrorStatus.NotEnoughMoney"
      #default
    >
      <BaseButton
        class="mint-modal-status-fail__button"
        appearance="secondary"
        @click="$emit('retry')"
      >
        Retry
      </BaseButton>
    </template>
    <template
      v-else-if="error === ErrorStatus.SomethingWrong"
      #default
    >
      <BaseButton
        class="mint-modal-status-fail__button"
        appearance="secondary"
        @click="$emit('try-again')"
      >
        Try again
      </BaseButton>
    </template>
    <template
      v-else-if="error === ErrorStatus.WrongChain"
      #default
    >
      <BaseButton
        class="mint-modal-status-fail__button"
        appearance="secondary"
        @click="switchNetwork(goerli.id)"
      >
        reconnect
      </BaseButton>
    </template>
  </MintStatus>
</template>

<script setup lang="ts">
import {computed} from 'vue';
import { useSwitchNetwork } from 'use-wagmi';
import {goerli} from 'viem/chains';
import MintStatus from './mint-modal-status.vue';
import {BaseButton} from '@/shared/ui/base-button';
import {ErrorStatus} from './constants';

interface Props {
  error: ErrorStatus
}

const props = defineProps<Props>();

const info = computed(() => {
  switch(props.error) {
    case ErrorStatus.NotEnoughMoney: {
      return {
        title: 'insufficient funds',
        text: 'Check your wallet balance and try again',
      };
    }
    case ErrorStatus.NotInWhitelist: {
      return {
        title: 'You are not on the white list',
        text: 'check our discord or twitter to get on it',
      }
    }
    case ErrorStatus.TokensSoldout: {
      return {
        title: 'all tokens are Sold out',
        text: 'check our Opensea account maybe smth was left there',
      }
    }

    case ErrorStatus.WrongChain: {
      return {
        title: 'wrong network',
        text: 'Switch to ethereum network',
      }
    }

    default: {
      return {
        title: 'Something went wrong',
        text: 'Please try again',
      }
    }
  }
});

const {
  switchNetwork
} = useSwitchNetwork();
</script>

<style lang="scss">
  .mint-modal-status-fail {
    &__button {
      @media (max-width: 650px) {
        flex-grow: 1;
      }
    }
  }
</style>
