import type { $Fetch } from 'ofetch';
import { $fetch } from 'ofetch';
import { httpParamsSerializer } from './http-params-serializer';

let httpClient: $Fetch;

export const useHttpClient = (): $Fetch => {
  if (httpClient !== undefined) return httpClient;

  httpClient = $fetch.create({
    baseURL: 'http://localhost:8000',//import.meta.env.BASE_URL,
    responseType: 'json',
    headers: {
      Accept: 'application/json',
    },
    onRequest: ({ request, options }) => {
      if (options.params) {
        // eslint-disable-next-line no-param-reassign
        options.params = httpParamsSerializer(options.params);
      }

      // eslint-disable-next-line no-console
      console.debug(`[FETCH LOG] ${new Date().toISOString()}`, request);
    },
  });

  return httpClient;
};
