const setVariables = () => {
  const bodyElement: HTMLElement | null = document.body
  const appElement: HTMLElement | null = document.querySelector('.vastega-app')
  const mainElement: HTMLElement | null = document.querySelector('main')

  if (window && bodyElement && appElement && mainElement) {
    const windowWidth = window.innerWidth
    const windowScrollY = window.scrollY
    const containerHeight = mainElement.getBoundingClientRect().height
    bodyElement.style.cssText = `--scrollTop: ${windowScrollY}px; --width: ${windowWidth}; --zoom: ${windowWidth / (1728 / 100)}%; --height: ${containerHeight}px;`
  }
}

export default setVariables
