<template>
  <div class="mint-modal-main-main">
    <div class="mint-modal-main-main__top">
      <BaseButton
          class="mint-modal-main-main__button"
          appearance="secondary"
          @click="inc"
      >
        <img src="./img/minus.svg" alt="Minus">
      </BaseButton>
      <div class="mint-modal-main-main__image">
        <MintModalCapsule class="mint-modal-main-main__image-image" />
        <div class="mint-modal-main-main__number">
          <Transition
              mode="out-in"
              name="fade"
          >
            <span :key="amount">
              {{ amount }}
            </span>
          </Transition>
        </div>
      </div>
      <BaseButton
          class="mint-modal-main-main__button mint-modal-main-main__button--right"
          appearance="secondary"
          @click="dec"
      >
        <img src="./img/plus.svg" alt="Plus">
      </BaseButton>
    </div>
    <div class="mint-modal-main-main__points">
      <div
          v-for="item in max"
          :key="item"
          class="mint-modal-main-main__point"
          :class="{
            'mint-modal-main-main__point--filled': item <= amount
          }"
          @click="amount = item"
      />
    </div>
    <p class="mint-modal-main-main__bottom-text">
      Avalable
    </p>
  </div>
</template>

<script setup lang="ts">
import {computed} from 'vue';
import { BaseButton } from '@/shared/ui/base-button';
import MintModalCapsule from './mint-modal-main-capsule.vue';

interface Props {
  modelValue: number;
  max: number;
}

const MIN = 1;

const props = defineProps<Props>();

const emits = defineEmits<{
  (e: 'update:model-value', value: Props['modelValue']): void;
}>();

const amount = computed({
  get() {
    return props.modelValue;
  },
  set(value) {
    emits('update:model-value', value);
  },
});

const inc = (): void => {
  if (amount.value != MIN) {
    amount.value -= 1;
  }
};

const dec = (): void => {
  if (amount.value != props.max) {
    amount.value += 1;
  }
};
</script>

