<template>
  <TransitionRoot
      as="template"
      :show="localModelValue"
      @close="localModelValue = false"
  >
    <Dialog as="div">
      <TransitionChild
          as="template"
          enter="animation-enter"
          enter-from="animation-enter-from"
          enter-to="animation-enter-to"
          leave="animation-leave"
          leave-from="animation-leave-from"
          leave-to="animation-leave-to"
      >
        <DialogPanel class="headless-dialog">
          <slot />
        </DialogPanel>
      </TransitionChild>
    </Dialog>
  </TransitionRoot>
</template>

<script setup lang="ts">
import { computed } from 'vue';
import { Dialog, DialogPanel, TransitionRoot, TransitionChild } from '@headlessui/vue';

interface Props {
  modelValue: boolean;
}

interface Emits {
  (e: 'update:model-value', value: Props['modelValue']): void,
}

const props = defineProps<Props>();

const emits = defineEmits<Emits>();

const localModelValue = computed({
  get() {
    return props.modelValue;
  },
  set(value) {
    emits('update:model-value', value);
  },
});
</script>

<style lang="scss">
.headless-dialog {
  position: fixed;
  inset: 0;
  z-index: 50;
}

.animation-enter, .animation-leave {
  transition: .2s ease-in-out;
}
.animation-enter-from, .animation-leave-to {
  opacity: 0;
}
.animation-enter-to, .animation-leave-from  {
  opacity: 1;
}
</style>
